@font-face {
    font-family: 'GmarketSansLight';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansLight.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'GmarketSansMedium';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'GmarketSansBold';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansBold.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@import url("http://fonts.googleapis.com/css?family=Roboto:400,300,100,500,700");

*, html, body{margin:0; padding:0;}
html, body{width:100%;}
html{font-size:16px; font-family:'GmarketSansMedium', 'Roboto', sans-serif; color:#333;}
body{position:relative; }
input:focus {outline:none;}
ul{list-style: none; }
div{box-sizing: border-box;}
.marginBottom{margin-bottom:100px; }
.padding{padding:0 10px; box-sizing: border-box;}


/* mainbanner */
#mainbanner{width:100%; background:url('bg.jpg')no-repeat; background-position:center center; background-size: cover;}
#mainbanner h1{padding:100px 0; text-align:center; font-size: 2rem; color:#fff;}

/* info */
#content{width:100%; max-width:1000px; margin:0 auto; }
ul.info{position: relative; top:-50px; left:0; width:100%;  display: flex; flex-direction:row; justify-content: space-between;}
ul.info li{width:32%; padding:30px 20px; background:#fff; box-shadow: 4px 4px 10px rgba(0,0,0,0.1); box-sizing: border-box; word-break:break-all; text-align:center; }
ul.info li p{margin-bottom:8px; font-size:0.75rem; }
ul.info li span{display: block; font-family:'GmarketSansBold', sans-serif; font-size:0.875rem; line-height: 1.6;}

/* instruction */
.instruction{width:100%; padding:15px 30px; border:1px solid #e1e1e1; border-radius:4px; line-height: 2.6;}
.instruction ShowInstruction{display:block; height:32px; background:url('arrow_down.png')no-repeat; background-position:top right; font-size:0.875rem;}

/* Input card number */
.cardNum_wrap{width:100%; text-align: center;}
.cardNum_wrap h2{font-size: 1.875rem; }
.cardNum_wrap span{display: block; margin-bottom:22px; font-size:0.875rem; color:#777;}
input{height:40px; width:310px; border:1px solid #e1e1e1; border-radius:4px; padding:0 14px; box-sizing: border-box; font-family:'GmarketSansMedium', 'Roboto', sans-serif; font-size: 0.875rem; color:#333;}
input::placeholder{font-size:0.75rem; color:#c7c7c7; }
input::-webkit-input-placeholder {font-size:0.75rem; color:#c7c7c7; }
input:-ms-input-placeholder {font-size:0.75rem; color:#c7c7c7; }
textarea::-webkit-input-placeholder {font-size:0.75rem; color:#c7c7c7; }
textarea:-ms-input-placeholder {font-size:0.75rem; color:#c7c7c7; }
Button{height:40px; width:310px; margin-top:8px; background:#0D6EFD; border:1px solid #0D6EFD; border-radius:4px; font-family:'GmarketSansMedium', 'Roboto', sans-serif; font-size:0.75rem; color:#fff; cursor:pointer;}

/* tokenInfo */
.tokenInfo_wrap{width:100%; background:#f1f1f1; padding-top:60px; padding-bottom:60px; text-align: center;}

#tokenInfo img{margin-bottom:24px;}
#tokenInfo h3{ font-family:'GmarketSansBold', sans-serif; font-size: 1.375rem; margin-bottom:10px; }
#tokenInfo p{width:100%; max-width:800px; margin:0 auto; margin-bottom:24px; word-break:break-all; font-size:0.875rem; color:#777;}

.tokenInfo_wrap ul{ width:100%; max-width:530px; margin:0 auto; }
.tokenInfo_wrap ul li{width:100%; overflow: auto; display: flex; flex-direction:row; justify-content: space-between; margin-bottom:8px;}
.tokenInfo_wrap label{ align-self: center;}
.tokenInfo_wrap input, .tokenInfo_wrap Form.Control{}
.tokenInfo_wrap Form.Control{height:40px; width:310px; margin-bottom:8px; border:1px solid #e1e1e1; border-radius:4px; padding:0 14px; box-sizing: border-box; font-family:'GmarketSansMedium', 'Roboto', sans-serif; font-size: 0.875rem; color:#333; }
.tokenInfo_wrap Button{margin-top:16px;}

/* morphedTokenInfo */
#morphedTokenInfo{width:100%; max-width:1000px; margin:0 auto; text-align:center; }
#morphedTokenInfo img{max-width:100%;}



@media ( max-width: 768px ) {
  .marginBottom{margin-bottom:60px; }
  #mainbanner{background:url('bg_m.jpg')no-repeat; background-position:center left; background-size: cover;}
  #tokenInfo img{width:100%; max-width:310px; }

  .tokenInfo_wrap ul li{flex-direction:column; align-items: center;}
  .tokenInfo_wrap label{margin-bottom:4px; }

}

@media ( max-width: 542px ) {
  ul.info{flex-direction:column; }
  ul.info li{width:100%; margin-bottom:10px; }
}
